.app {

    height: 100%;
    max-width: 100%;
    margin: 20px 30px;
    z-index: 1;
}

.buttonContainer {
    position: relative;
    padding-bottom: 35px;
}

.buttonModal {
    position: absolute;
    width: 110px;
    right: 5px;
}

.react-icons {
    position: relative;
    left: 0;
    vertical-align: middle;
}

@media screen and (max-width: 600px) {
    .app {
        margin: 20px 0;
        overflow-y: auto;
    }

    .buttonContainer {
        position: static;
        display: flex;
        justify-content: center;
    }

    .buttonModal {
        position: static;


    }
}
